.section--review {
	margin-bottom: 100px;

	.section__title {
		h2 {
			@include r(767) {
				margin-bottom: 30px; }

			span {
				display: block;

				@include r(767) {
					display: inline-block; } } } } }

.review {
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;

	@include r(767) {
		flex-flow: column wrap; } }

.review__text {
	h3 {
		position: relative;
		font-weight: 700;
		font-size: 24px;
		margin-bottom: 35px;

		&::after {
			content: '';
			position: absolute;
			left: 0;
			bottom: -15px;
			width: 80px;
			height: 4px;
			background-color: $purple; } } }

.review__item {
	display: flex;
	flex-flow: row nowrap;
	justify-content: flex-start;
	align-items: flex-start;
	padding-top: 100px;

	@include r(1400) {
		flex-direction: column;
		align-items: center;
		flex-wrap: wrap; } }

// TODO: Fix image clip

.review__avatarWrapper {
	position: relative;
	padding-right: 70px;

	&::after {
		content: '';
		position: absolute;
		top: 30px;
		right: 0;
		display: block;
		width: 60px;
		height: 60px;
		background-image: url(../img/icon-quote.svg); }

	@include r(1400) {
		max-width: 630px;
		width: 100%; } }

.review__textWrapper {
	position: relative;
	max-width: 300px;
	width: 100%;
	margin-top: 60px;
	margin-right: 20px;

	@include r(1400) {
		max-width: 100%; }

	p {
		margin-bottom: 16px; } }

.review__feedPagination ul {
	max-width: 200px;
	width: 100%;
	height: 425px;
	background-color: $dark;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;

	@include r(767) {
		max-width: 100%;
		height: 200px;
		flex-direction: row; }

	li {
		font-size: 40px;
		line-height: 1.5;
		text-align: center;
		color: $white;
		margin-bottom: 30px;

		@include r(767) {
			margin-bottom: 0; }

		&::last-child {
			margin-bottom: 0; }

		&.review__current {
			color: $dark; } } }

.review__feed {
	height: 1300px;
	overflow-y: scroll;
	-ms-overflow-style: none;
	scrollbar-width: none;

	&::-webkit-scrollbar {
		width: 0;
		background: transparent; } }

.review__current {
	width: 120px;
	height: 120px;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: $white;
	border-radius: 50%; }

.review__name {
	margin-top: 20px;
	padding-top: 15px;
	border-top: 2px solid $dark;
	font-size: 18px;
	font-weight: 500; }

.review__position {
	opacity: 0.5;
	font-weight: 600; }
