.footer {
	background-color: $dark;

	.container {
		padding: 25px 115px;
		display: flex;
		flex-flow: row nowrap;
		justify-content: space-between;
		align-items: center; } }

.footer__logo, footer__copyright, .footer__socials {
	flex: 1 0; }

.footer__logo {
	svg {
		width: 70px; }

	.logoStroke {
		stroke: $white; }

	.logoRect {
		fill: $white; } }

.footer__copyright {
	span {
		color: $white;
		font-size: 11px; } }

.footer__socials {
	.iconColor {
		fill: $white; } }
